.sticky-socials {
  position: fixed;
  display: flex;
  background-color: var(--primaryColor);
  top: 33%;
  right: 0;
  width: 5vw;
  height: 15vw;
  flex-direction: column;
  align-items: center;
  z-index: 9999;
  justify-content: center;
  border-radius: 15px 0 0 15px;
  transition: all 0.3s;
}
.sticky-socials:hover {
  width: 6vw;
  background-color: #001b4d;
}
.social-link {
  color: white;
  font-size: 2vw;
  transition: all 0.3s;
}
.social-link:hover {
  color: var(--tertiaryColor);
}
