section.single-featured-image {
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  min-width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  align-content: center;
  justify-content: flex-end;
}
h1.title.title-page {
  text-align: center;
  padding: 5% 6%;
  margin-bottom: -7.9%;
  font-size: 2rem;
  background: #008ccc;
  color: white;
}
.container.content {
  margin-top: 20rem;
}
.page-wraper {
  background-color: #1b252f;
  color: white;
}

.content {
  font-size: 1.3vw;
  padding-bottom: 9rem;
}
