/* ======================================== */
section.marcas {
  background-color: white;
  margin-bottom: -1%;
}
/* Slider */
.slick-slide {
  margin: 0px 20px;
}

/* .logo-carousel {
    overflow: inherit;
    border-top: 1px solid #353535;
    border-bottom: 1px solid #353535;
  } */

.slick-slide img {
  width: 100%;
}

.slick-track::before,
.slick-track::after {
  display: table;
  content: "";
}

.slick-track::after {
  clear: both;
}

.slick-track {
  padding: 1rem 0;
  display: flex !important;
  align-items: center;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-arrow {
  position: absolute;
  top: 40%;
  /* background: url(https://raw.githubusercontent.com/solodev/infinite-logo-carousel/master/images/arrow.svg?sanitize=true) center no-repeat; */
  color: var(--primaryColor);
  /* filter: invert(100%) sepia(32%) saturate(1%) hue-rotate(
  344deg
  ) brightness(105%) contrast(103%); */
  border: none;
  width: 3vw;
  background: transparent;
  height: 2.3vw;
  /* text-indent: -10000px; */
  margin-top: -16px;
  z-index: 99;
}
i.fas.fa-chevron-right,
.fa-chevron-left {
  color: inherit;
  width: 100%;
  font-size: 3vw;
}

.slick-arrow.slick-next {
  right: -40px;
  /* transform: rotate(180deg); */
}

.slick-arrow.slick-prev {
  left: -40px;
}

/* Media Queries */

@media (max-width: 768px) {
  .slick-arrow {
    width: 1rem;
    height: 1rem;
  }
}

body {
  background-color: #010101;
}

.row {
  overflow: hidden;
}

