/* ======================================== */
section.categorias {
  margin-top: 5%;
  padding-bottom: 6%;
}
.categoria {
  height: 25vw;
  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  background-size: 100%;
  background-position: center;
  transition: all 1.3s;
}
.categoria:hover {
  background-size: 133%;
}
/* .cat-link-wraper {
    position: relative;
  } */
.title-3.cat-title {
  font-size: 1.6vw;
  padding-left: 6%;
  margin-top: 6%;
  transition: all 0.4s;
}
a.cat-link-wraper:hover {
  text-decoration: none;
}
.categoria:hover a.cat-link-wraper h3.title-3.cat-title {
  padding-left: 13%;
}
/* .img-cat {
    position: absolute;
    width: 100%;
  } */
img.img-cat {
  position: absolute;
  width: 25vw;
  bottom: 16%;
  left: 6%;
  transition: all 0.4s;
}
.categoria:hover a.cat-link-wraper img.img-cat.carros {
  width: 27vw;
  bottom: 19%;
  left: 5%;
}
.categoria:hover a.cat-link-wraper img.img-cat.veic {
  width: 27vw;
  bottom: 18%;
  left: 2%;
}
img.img-cat.moto {
  width: 20vw;
  left: 12%;
}
.categoria:hover a.cat-link-wraper img.img-cat.moto {
  width: 23vw;
  left: 15%;
}
img.img-cat.estacio {
  bottom: 0;
  width: 36vw;
  right: 0%;
  left: auto;
}
.categoria:hover a.cat-link-wraper img.img-cat.estacio {
  width: 38vw;
  right: 12%;
  bottom: -6%;
}
img.img-cat.tracio {
  bottom: 9%;
  left: 12%;
  width: 23vw;
}
.categoria:hover a.cat-link-wraper img.img-cat.tracio {
  left: 15%;
  width: 26vw;
}
img.img-cat.nauti {
  bottom: 20%;
  left: 0%;
}
.categoria:hover a.cat-link-wraper img.img-cat.nauti {
  bottom: 6%;
  /* left: 3%; */
  width: 30vw;
}
