/* ================================================== */
/* =====================CARDBOARD===================== */
/* ================================================== */

section.cards > .container > .row {
  overflow: visible;
}
section.cards {
  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  background-size: cover;
  background-position: center;
  min-width: 100%;
  padding: 16% 0 10% 0;
}
.cardboard-wraper {
  background-color: rgb(227, 231, 233);
  min-height: 35rem;
  border-radius: 3rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  -webkit-box-shadow: 0px 14px 20px 0px rgb(0 0 0 / 85%);
  -moz-box-shadow: 0px 14px 20px 0px rgba(0, 0, 0, 0.85);
  box-shadow: 0px 14px 20px 0px rgb(0 0 0 / 85%);
  transition: all 0.6s;
}
.cardboard-wraper:hover {
  -webkit-box-shadow: 0px 47px 46px 10px rgb(0 0 0 / 71%);
  -moz-box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
  box-shadow: 0px 47px 46px 10px rgb(0 0 0 / 71%);
  transform: translateY(-40px) scale(1.15);
}

img.cardboard-img {
  position: absolute;
  top: -30%;
  width: 20vw;
  transition: all 0.5s;
}
.cardboard-wraper:hover img.cardboard-img {
  position: absolute;
  top: -43%;
  width: 23vw;
  left: 13%;
}
.cardboard {
  min-height: 25rem;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-between;
  padding: 28% 10% 0 10%;
}
.cardboard-title {
  font-size: 1.7vw;
  text-align: center;
}
.cardboard-title > span {
  font-size: 1.2vw;
}
.cardboard-body {
  font-size: 1vw;
  font-weight: 600;
  margin-bottom: 5%;
}
a.cardboard-footer {
  width: 100%;
  text-align: center;
  background: var(--secondaryColor);
  border-radius: 0 0 2.5rem 2.5rem;
  padding: 5% 0;
  border-top: #71272f 1rem solid;
  transition: all 0.6s;
}
.cardboard-wraper > a.cardboard-footer:hover {
  text-decoration: none;
  border-top: var(--tertiaryColor) 1rem solid;
}
a.cardboard-footer:hover .title-3 {
  color: var(--tertiaryColor);
  font-weight: 900;
  letter-spacing: 0.4vw;
}
a.cardboard-footer > .title-3 {
  font-size: 2vw;
  font-weight: 700;
  transition: all 0.3s;
  letter-spacing: 0.3vw;
}
img.cardboard-img.nd-card {
  width: 30vw;
  top: -49%;
}
.cardboard.nd-card {
  padding: 20% 10% 0 10%;
}
h3.title-3.nd-card.cardboard-title.white {
  background: var(--primaryColor);
  font-size: 1.3vw;
  padding: 5% 3%;
  border-radius: 1.5vw;
  border: solid 0.5vw white;
}
.cardboard.nd-card {
  padding: 16% 10% 0 10%;
  z-index: 999;
}
h3.primary.title-3.nd-card.cardboard-title.cardboard-subtitle {
  font-size: 1.5vw;
}
.cardboard-body.nd-card.gray {
  font-size: 1.6vw;
  text-align: center;
}
.cardboard-body.nd-card.gray > div > i {
  background: var(--primaryColor);
  padding: 3%;
  border-radius: 3vw;
  color: white;
  font-size: 1.2vw;
  margin-right: 1vw;
}

a.cardboard-footer.nd-card {
  background: var(--primaryColor);
  border-top: #2c385a 1rem solid;
}
.cardboard-wraper:hover img.cardboard-img.nd-card {
  position: absolute;
  top: -39%;
  width: 33vw;
  left: auto;
}
img.cardboard-img.rd-card {
  width: 24vw;
  top: -41%;
}
h3.title-3.rd-card.cardboard-title {
  color: #00afc6;
  font-size: 2vw;
  font-weight: 700;
  margin-top: 1vw;
}
a.cardboard-footer.rd-card {
  border-top: #008689 1rem solid;
  background-color: #00afc6;
}
.cardboard.rd-card {
  padding: 23% 10% 0 10%;
}
.cardboard-body.rd-card {
  font-size: 1.3vw;
}
.cardboard-wraper:hover img.cardboard-img.rd-card {
  top: -53%;
  width: 29vw;
  left: auto;
}
@media (min-width: 1400px) {
  .cardboard-body.rd-card {
    font-size: 1vw;
  }
}

