.btn-wk {
  position: relative;
  border-radius: 100px;
  font-size: 1.5rem;
  padding: 1.5% 6%;
  transition: all 0.5s;
  font-weight: 800;
  letter-spacing: 0.3rem;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
}
.btn-wk:active {
  transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgb(0 0 0 / 20%);
}
.btn-wk:hover {
  transform: translateY(-4px);
  -webkit-box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
  -moz-box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
  box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
}
a.btn-wk:hover {
  color: inherit;
  text-decoration: none;
}
.btn-wk::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.4s;
  background-color: inherit;
}
.btn-wk:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
.btn-wraper {
  display: flex;
  justify-content: center;
  &.search {
    width: 100%;
  }
}
.btn-wraper > .btn-wk {
  padding: 3% 9%;
}
button:focus {
  outline: 0px dotted;
  outline: 0px auto transparent !important;
}
